@import "@/assets/scss/variables.scss";































.layout-default {
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  background-color: $color-gray-smoke;

  &__header {
    height: $header-height;
    background-color: $color-brand;
  }

  &__main {
    display: flex;
    flex-grow: 1;
    align-items: stretch;
    margin-right: 80px;
    margin-left: 80px;

    &-inner {
      width: 100%;
      max-width: 1024px;
      min-height: 100%;
      margin: 0 auto;
      background-color: white;
      box-shadow: $box-shadow;
    }
  }

  &__footer {
    //
  }
}
